:root {
    --main-purple: #7d52a1;
    --main-purple-hover: #633589;
    --main-red: #d71f2e;
    --main-red-hover: #aa0f18;
    --main-pink: #ec008c;
    --main-pink-hover: #de0082;
    --main-orange: #eb983f;
    --main-orange-hover: #d17818;
    --main-light-grey: #f2f2f2;
    --main-grey: #dddddd;
}

body {
    font-family: 'Open Sans', Helvetica, Arial, Lucida, sans-serif;
    margin: 0px;
}

.alert {
    text-align: center;
    color: #fff;
    background-color: #d71f2e;
    margin: 50px;
}

.form {
    /* background-color: #dbd5d5; */
    background-color: #f2f2f2;
    background-color: var(--main-light-grey);
    width: 30%;
    height: 250px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 2px;
}

input {
    margin: 5px;
    margin-bottom: 10px;
}

.log-in, .log-out, .register {
    background-color: #ec008c;
    background-color: var(--main-pink);
    border-radius: 4px;
    color: #f8f8f8;
    min-height: 50px;
    min-width: 100px;
    outline:0;
    cursor: pointer;
}

.log-in:hover, .log-out:hover, .register:hover {
    background-color: #de0082;
    background-color: var(--main-pink-hover);
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #444; 
    cursor: pointer;
}

a {
    text-decoration: none;
}

.paper {
    padding: 65px 0px 58px 0px;
}
.nav-title {
    font-size: 24px;
    font-weight: 700;
    display: inline-block;
    color: var(--main-light-grey);
    padding-left: 10px;
}

.nav {
  overflow: hidden;
  background-color: var(--main-purple);
}

.nav-link {
  float: left;
  color: var(--main-light-grey);
  background-color: var(--main-purple);
  text-align: center;
  padding: 24px 10px;
  text-decoration: none;
  font-size: 15px;

  /* these last two make the logout button in
  the nav bar look like another link */
  border:none;
  cursor: pointer;
  outline:0;
}

.nav-link:hover {
  background-color: var(--main-purple-hover);
}

.nav-right {
  float: right;
}
.footer {
    padding: 20px;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    background-color: var(--main-purple);
    color: 'white';
    position: fixed;
    bottom: 0;
    /* left: 0; */
    width: 100%;
}

.ellipsis-text {
  margin-top: 5px;
  font-size: 0.875rem;
  font-weight: 400;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  line-height: 1.46429em;
}
